.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: rgb(134, 67, 67);
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

body {
margin: 0 auto 0 auto;
padding: 0;
max-width: 1200px;
font-family: "Avenir", "Avenir Next", Helvetica Neue, Arial;
font-size: 0.95em;
}
a, a:visited { text-decoration: none; color: #7533f4; }
a:hover { text-decoration: underline; color: #f4b014; }
h1, h2, h3, h4, h5 {
color: #492a7c;
background-color: inherit;
font-weight: normal;
padding: 0 0 5px 0;
margin: 15px 0 0 0;
border: none;
clear: right;
}
h1 { font-size: 24pt; margin:  5px 0 10px 0; line-height: 28px; }
h2 { font-size: 14pt; margin: 30px 0 15px 0; letter-spacing: 0.01em; border-bottom: 1px solid #ccc;  line-height: 20px;}
h3 { font-size: 13pt; }
h4 { font-size: 12pt; }
h5 { font-size: 11pt; }
p { margin: 0 0 10px 0; }
.content {
margin: 0;
padding: 15px 20px;
background-color: #ffffff;
}
.title, .title h1, .title a {
color: #492a7c;
font-size: 24pt;
margin-bottom: 20px;
margin-top: 5px;
}
.footer {
border-top: 1px solid #ccc;
margin-top: 30px;
padding-top: 4px;
text-align: right;
font-size: 12px;
}
.footer a {
color: #21346B;
}
.footer a:hover {
color: #ce3333;
}

